<template>
    <div class="shandui">
        <van-nav-bar
                title="闪兑"
                left-arrow
                right-text="闪兑记录"
                @click-left="onClickLeft"
                @click-right="onClickRight"
                fixed
        ></van-nav-bar>
        <div style="margin-top:15vw;">
            <div class="form-box">
                <van-field
                        v-model="remain"
                        label="SHT单价"
                        readonly
                        disabled
                        input-align="right"
                ></van-field>
                <div style="width: 98vw;">
                    <div style="width: 100%;padding: 4vw 0px; ">
                        <van-row>
                            <van-col span="8" style="text-align: left;padding-left: 15px;box-sizing: border-box;">
                                <span style="color: red;margin-right: 5px;">从</span>{{label1}}
                            </van-col>
                            <van-col span="8">
                                <span  v-on:click="changeRule">
                                    <van-icon name="exchange"></van-icon>
                                </span>
                            </van-col>
                            <van-col span="8" style="text-align: right;padding-right: 15px;box-sizing: border-box;">
                                <span style="color: green;margin-right: 5px;">到</span>{{label2}}
                            </van-col>
                        </van-row>

                    </div>
                    <div class="van-hairline--bottom"  style="width: 96%;margin-left: 4%;height: 1px;"></div>
                </div>
                <van-field
                        v-model="takenum"
                        :label="'兑换'+label2+'数量'"
                        placeholder="请输入兑换数量"
                        label-width="25vw"
                ></van-field>
                <van-field
                        v-model="realnum"
                        :label="'扣除'+label1+'数量'"
                        readonly
                        disabled
                        input-align="right"
                        label-width="25vw"
                ></van-field>
                <van-field
                        v-model="walet_password"
                        label="交易密码"
                        placeholder="请输入交易密码"
                        type="password"
                        required
                ></van-field>
            </div>
            <van-button type="info" style="width: 90vw;font-weight: bold;margin-top: 8vw;">确认兑换</van-button>
        </div>
        <van-popup
                v-model="show_shandui_record"
                position="right"
                get-container="#app"
                :style="{width:'100vw', height:'100vh', background:'white'}"
                :duration="0.3"
                :overlay="false"
        >
            <shandui-record></shandui-record>
        </van-popup>
    </div>
</template>

<script>
    import { NavBar, Popup, Field, Button, Row, Col, Icon } from 'vant';
    import shanduiRecord from '@/components/common/record/shanduiRecord.vue';
    export default {
        name: "takecash",
        components: {
            [NavBar.name]: NavBar,
            [Popup.name]: Popup,
            [Field.name]: Field,
            [Button.name]: Button,
            [Row.name]: Row,
            [Col.name]: Col,
            [Icon.name]: Icon,
            shanduiRecord
        },
        data() {
          return {
              label1: 'SHT', // from label1
              label2: 'QC', // to label2
              show_shandui_record: false, // 是否跳转提现记录
              remain: '0.32656', // SHT单价
              takenum: '', //提现数量
              realnum: 0, // 扣除数量
              walet_password: '', // 交易密码
          }
        },
        methods: {
            onClickLeft () {
                // this.$parent.$parent.poup_show = false;
                this.$router.go(-1);
            },
            onClickRight () {
                // this.show_shandui_record = true
                this.$router.push({path:'/shanduiRecord'})
            },
            onClickRightIcon () {
                this.show_walet_addr_options = true;
            },
            SelectionRadio (item) {
                this.walet_addr = item.walet_addr;
                this.show_walet_addr_options = false;
            },
            getCode () {
                if(!this.get_code_status){
                    this.get_code_status = true;
                    let _this = this;
                    this.sms_button_text = '60s';
                    this.code_count_down_handle = setInterval(function () {
                        console.log(_this.sms_button_text)
                        if(_this.init_sms == 0){
                            _this.sms_button_text = '获取验证码';
                            _this.init_sms = 59;
                            clearInterval(_this.code_count_down_handle);
                            this.get_code_status = false;
                            return false;
                        }
                        _this.sms_button_text = _this.init_sms+'s';
                        _this.init_sms--;
                    }, 1000);
                }
            },

            changeRule(){
                console.log('sss');
                let a = this.label2;
                this.label2 = this.label1;
                this.label1 = a;
            }
        }
    }
</script>

